import "./App.css";
import Navigation from "./utils/navigation/navigation";
import React, { useRef, useCallback, useState, useEffect } from "react";
import ReactPlayer from "react-player";
import myvideo from "./videos/earth.mp4";
import SplitText from "./test";
import Experience from "./Experience";
import Certificates from "./Certificates";
import Backtotop from "./utils/footer/Backtotop";
import Aboutme from "./Aboutme";
import Projects from "./Projects";
import Footer from "./Footer";
import Education from "./Education";

function App() {
  const videoRef = useRef(null);
  const [scrollY, setScrollY] = useState(0);

  useEffect(() => {
    const handleScroll = () => {
      setScrollY(window.scrollY);
    };

    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  // Calculate opacity and transform based on scroll
  const opacity = Math.max(1 - scrollY / 500, 0);
  const scale = Math.max(1 - scrollY / 2000, 0.95);
  const translateY = -scrollY * 0.5;

  return (
    <div className="App">
      <Navigation />
      <Backtotop />
      {/* <div className="header">
        <ReactPlayer
          className="player"
          width={"100vw"}
          height={"100vh"}
          z-index={-1}
          url={myvideo}
          playing={false}
          ref={videoRef}
        /> 
        <SplitText />
      </div> */}
      <div
        className="mobile-intro"
        style={{
          opacity: opacity,
          transform: `translateY(${translateY}px) scale(${scale})`,
        }}
      >
        <span className="intro-name">Hi, I'm Prasanna Nivas</span>
        <span className="intro-title">Full Stack Developer</span>
      </div>
      <div className="intro-spacer"></div>
      <Aboutme />
      <Education />
      <Experience />
      <Certificates />
      <Projects />
      <Footer />
    </div>
  );
}

export default App;
